import React from "react";
import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import key from "../../../util/key";
import Layout4 from "../../../util/Layout4";
import {
  Button,
  Form,
  Input,
  Tree,
  Popconfirm,
  Space,
  Tooltip,
  message,
  Typography,
  Alert,
} from "antd";
import Context from "../../../Context";
import {
  PlusOutlined,
  DeleteOutlined,
  UsergroupAddOutlined,
} from "@ant-design/icons";
import paramsHasChanged from "../../../util/paramsHasChanged";
import Title from "../Title";

const { Text } = Typography;

class Groups extends React.Component {
  static contextType = Context;
  constructor(props) {
    super(props);
    this.state = {
      treeSearch: "",
      treeKey: key(),
      tree: [],
    };
  }

  pendingStart = () => {
    this.setState({ pending: true });
  };
  pendingEnd = () => {
    this.setState({ pending: false });
  };

  componentDidMount() {
    this.setParams();
    this.load();
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (paramsHasChanged(prevProps, this.props)) {
      this.setParams();
    }

    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.load();
    }
  }

  setParams = () => {
    const { param1: groupId } = this.props.match.params;
    this.setState({
      groupId,
      treeKey: key(),
    });
  };

  load = async () => {
    this.pendingStart();
    let search = [];
    if (this.state.treeSearch) {
      search.push({
        field: "name",
        compare: "IKE",
        value: `%${this.state.treeSearch}%`,
      });
    }
    let tree = await this.context.api.q(
      "/admin/group/list",
      { search },
      { toTree: true }
    );
    this.setState({
      tree: tree || [],
      treeKey: key(),
    });
    this.pendingEnd();
  };

  save = async (data) => {
    const { groupId } = this.state;
    const { intl } = this.props;

    let saveData = {
      ...data,
      id: groupId && groupId !== "add" ? groupId : null,
    };

    this.pendingStart();
    let result = await this.context.api.q("/admin/group/set", saveData);

    if (result === false) {
      this.pendingEnd();
      message.error(intl.formatMessage({ id: "save.fail" }));
    } else {
      message.success(intl.formatMessage({ id: "save.success" }));
      await this.load();
      if (groupId === "add" && result.id) {
        this.props.history.push(`/admin/groups/${result.id}/`);
      }
    }
  };

  getItem = () => {
    const { groupId, tree } = this.state;
    return tree.find((item) => item.id === groupId) || {};
  };

  onTreeSelect = (selectedKeys) => {
    if (Array.isArray(selectedKeys)) {
      if (selectedKeys[0]) {
        this.props.history.push(`/admin/groups/${selectedKeys[0]}`);
      }
    }
  };
  onTreeSearch = (value) => {
    this.setState({ treeSearch: value }, () => this.load());
  };

  onAddStart = () => {
    this.props.history.push(`/admin/groups/add`);
  };

  onDelete = async () => {
    const { intl, history } = this.props;
    const { groupId } = this.state;
    this.pendingStart();

    let result = await this.context.api.q("/admin/group/delete", {
      id: groupId,
    });
    if (result !== false) {
      message.success(intl.formatMessage({ id: "delete.success" }));
      history.push(`/admin/groups/`);
      this.load();
    } else {
      message.error(intl.formatMessage({ id: "delete.fail" }));
      this.pendingEnd();
    }
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.groups." + key });
  };

  render() {
    const { pending, treeKey, groupId } = this.state;
    const { intl } = this.props;
    const item = this.getItem();
    return (
      <>
        <Title
          title={this.m("groups")}
          url="/admin/groups"
          icon={<UsergroupAddOutlined />}
        />
        <Layout4 pending={pending} onSearch={this.onTreeSearch}>
          <div>
            {item.id && (
              <>
                <Text type="secondary">{item.name}</Text>
              </>
            )}
          </div>
          <div className="isoNoteBtns">
            <Space>
              <Tooltip title={this.m("btn.add")}>
                <Button
                  onClick={this.onAddStart}
                  type="primary"
                  icon={<PlusOutlined />}
                />
              </Tooltip>
              {groupId && groupId !== "add" && (
                <Popconfirm
                  title={`${intl.formatMessage({
                    id: "delete.sure",
                  })}`}
                  onConfirm={() => this.onDelete()}
                  okText={intl.formatMessage({ id: "yes" })}
                  cancelText={intl.formatMessage({ id: "no" })}
                >
                  <Tooltip title={this.m("btn.delete")}>
                    <Button icon={<DeleteOutlined />} />
                  </Tooltip>
                </Popconfirm>
              )}
            </Space>
          </div>
          <Tree
            key={treeKey}
            defaultExpandAll
            defaultExpandParent
            defaultSelectedKeys={[groupId]}
            defaultExpandedKeys={[groupId]}
            treeData={this.state.tree}
            onSelect={this.onTreeSelect}
          />

          <div>
            {item.system && (
              <Alert
                style={{ marginBottom: "15px" }}
                type="warning"
                message={this.m("system")}
              />
            )}
            {groupId && (
              <Form
                key={item.id}
                layout="vertical"
                name="type"
                initialValues={{
                  name: item.name || "",
                  username: item.username || "",
                  description: item.description || "",
                }}
                onFinish={this.save}
              >
                <Space direction="vertical" style={{ width: "100%" }}>
                  <Form.Item
                    rules={[{ required: true }]}
                    label={this.m("name")}
                    name="name"
                  >
                    <Input />
                  </Form.Item>

                  <Form.Item
                    rules={[{ required: true }]}
                    label={this.m("username")}
                    name="username"
                  >
                    <Input />
                  </Form.Item>

                  <Form.Item label={this.m("description")} name="description">
                    <Input />
                  </Form.Item>

                  <Form.Item>
                    <Space>
                      <Button
                        loading={pending}
                        type="primary"
                        htmlType="submit"
                      >
                        {intl.formatMessage({
                          id: "save",
                        })}
                      </Button>
                    </Space>
                  </Form.Item>
                </Space>
              </Form>
            )}
          </div>
        </Layout4>
      </>
    );
  }
}

export default withRouter(injectIntl(Groups));
