import React from "react";
import { Scrollbars as RCS } from "react-custom-scrollbars";

const CustomScrollbars = ({ id, style, children, className }) => (
  <RCS
    id={id}
    className={className}
    style={style}
    autoHide
    autoHideTimeout={1000}
    autoHideDuration={200}
    autoHeightMin={0}
    autoHeightMax={200}
    thumbMinSize={30}
    universal={true}
  >
    {children}
  </RCS>
);

export default CustomScrollbars;
