export default function paramsHasChanged(prevProps, props) {
  const prevParams = prevProps.match.params || {};
  const nowParams = props.match.params || {};

  if (
    prevParams.param1 !== nowParams.param1 ||
    prevParams.param2 !== nowParams.param2 ||
    prevParams.param3 !== nowParams.param3
  ) {
    return true;
  }

  return false;
}
