import React from "react";
import { Input } from "antd";
import { injectIntl } from "react-intl";
let searchTimeout = null;

class Searchbox extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      searchValue: props.defaultValue || "",
      searchPending: false,
      searchDelay: props.hasOwnProperty("searchDelay")
        ? props.searchDelay
        : 1000,
    };
  }
  searchHandle = (searchValue) => {
    this.setState({ searchValue });
    if (!this.state.searchDelay) {
      if (this.props.onSearch) {
        this.props.onSearch(searchValue);
        return;
      }
    }
    this.setState({ searchPending: true });
    window.clearTimeout(searchTimeout);
    searchTimeout = window.setTimeout(() => {
      this.setState({
        searchPending: false,
      });
      if (this.props.onSearch) {
        this.props.onSearch(searchValue);
      }
    }, this.state.searchDelay);
  };

  searchOnChange = (e) => {
    this.searchHandle(e.target.value);
  };
  render() {
    const { intl, className } = this.props;
    const { searchValue, searchPending } = this.state;
    let placeholder =
      this.props.placeholder ||
      intl.formatMessage({ id: "search.placeholder" });
    return (
      <Input.Search
        placeholder={placeholder}
        className={className}
        value={searchValue}
        onChange={this.searchOnChange}
        onSearch={() => this.searchHandle(searchValue)}
        loading={searchPending}
      />
    );
  }
}
export default injectIntl(Searchbox);
