import React from "react";
import IntlMessages from "./IntlMessages";
import Scrollbars from "./Scrollbars";
import Searchbox from "./Searchbox";
import { Layout, Spin } from "antd";
import { injectIntl } from "react-intl";
const { Header, Content } = Layout;

class Layout4 extends React.Component {
  render() {
    const [
      childTopLeft,
      chilfTopRight,
      childLeft,
      childRight,
    ] = this.props.children;

    return (
      <>
        <Spin spinning={this.props.pending ? true : false}>
          <div className="isomorphicNoteComponent">
            <div style={{ width: "340px" }} className="isoNoteListSidebar">
              <div className="isoNoteListWrapper">
                {this.props.onSearch && (
                  <Searchbox
                    placeholder={this.props.searchPlaceholder}
                    className="isoSearchNotes"
                    onSearch={this.props.onSearch}
                    searchDelay={this.props.searchDelay}
                  />
                )}

                <div className="isoNoteList">
                  {childLeft ? (
                    <Scrollbars>
                      <div className="isoNoteListHolder">{childLeft}</div>
                    </Scrollbars>
                  ) : (
                    <span className="isoNoResultMsg">
                      <IntlMessages id="nothing.found" />
                    </span>
                  )}
                </div>
              </div>
            </div>
            <Layout className="isoNotepadWrapper">
              <Header className="isoHeader">
                {childTopLeft}
                {chilfTopRight}
              </Header>
              <Content className="isoNoteEditingArea">
                {childRight && (
                  <Scrollbars>
                    <div className="isoNoteContentHolder">{childRight}</div>
                  </Scrollbars>
                )}
              </Content>
            </Layout>
          </div>
        </Spin>
      </>
    );
  }
}

export default injectIntl(Layout4);
